import React, { useEffect, useState } from "react";
import styled from "@emotion/styled";
import Card from "./Cards";
import axios from "axios";
import FeaturedCard from "./FeaturedCard";

const Container = styled.div`
  width: 80vw;
  padding: 10px 6%;
  margin-left: 3.5rem;
  margin-bottom: 50px;
  @media (max-width: 1230px) {
    padding: 0;
    margin: 0;
    width: 100vw;
  }
`;

const ContentRecent = styled.div`
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  @media (max-width: 1000px) {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
  @media (max-width: 700px) {
    grid-template-columns: repeat(1, minmax(0, 1fr));
  }
`;

const ContentFeatured = styled.div`
  display: grid;
  grid-template-columns: repeat(4, minmax(0, 1fr));
  @media (max-width: 1000px) {
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }
  @media (max-width: 700px) {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
`;

const Text = styled.p`
  margin-left: 20px;
  font-size: 24px;
  align-items: center;
  justify-content: space-between;
`;

const ProductsRow = () => {
  const [recentProducts, setRecentProducts] = useState([]);
  const [featuredProducts, setFeaturedProducts] = useState([]);
  useEffect(() => {
    fetchData();
  }, []);
  const fetchData = async () => {
    let temp = await axios.get(
      `${process.env.React_APP_BASE_URL}/product/getlatest`
    );
    let temp2 = await axios.get(
      `${process.env.React_APP_BASE_URL}/product/featured`
    );
    setRecentProducts(temp.data);
    setFeaturedProducts(temp2.data);
  };
  return (
    <div style={{ backgroundColor: "white" }}>
      <Container>
        <Text>
          <b>Featured Products</b>
        </Text>
        <ContentFeatured>
          {featuredProducts.map((item) => {
            return (
              <Card
                name={item.name}
                price={item.price}
                image={`${process.env.React_APP_BASE_URI}${item.image[0]}`}
                description={item.description}
                product={item}
              />
            );
          })}
        </ContentFeatured>
        <Text>
          <b>Recent Products</b>
        </Text>
        <ContentRecent>
          {recentProducts.map((item) => {
            return (
              <FeaturedCard
                name={item.name}
                price={item.price}
                image={`${process.env.React_APP_BASE_URI}${item.image}`}
                description={item.description}
                product={item}
              />
            );
          })}
        </ContentRecent>
      </Container>
    </div>
  );
};

export default ProductsRow;
